 */
header {
  position: fixed;
  padding: 0.5rem 0;
  min-height: 90px;
  z-index: 40;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* font-family: Georgia, 'Times New Roman', Times, serif; */
  color: #ebd693;
  background-color: #2a2a2a;
  /* background: \$gradient-background; */
  border-bottom: 2px solid #ebd693;
}

a {
  font-size: 1rem;
  background-color: none;
  margin: 0 1.5rem;
  cursor: pointer;
}

a:active {
  transform: scale(1.01);
  transition: 0.2s;
}

#home-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 5rem;
}
#home-tab #home-heading {
  /* font-family: 'Rozha One', serif; */
  color: #a3771f;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 2rem;
  text-shadow: 0.8px 0.8px #4fa9cc;
  letter-spacing: 1.8px;
}
#home-tab #home-sub-heading {
  font-size: 1rem;
  color: white;
  margin-top: -0.15rem;
}

#right-side {
  padding-right: 2rem;
}

#right-side a {
  text-transform: uppercase;
}

#right-side a.active {
  border-bottom: 4px solid #4fa9cc;
  transition: border-width 0.2s ease-in-out;
}

#menu-button {
  display: none;
}

#dropdown-box {
  position: absolute;
  top: 0;
  transition: all 0.33s;
}

#dropdown-box.active {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  z-index: 1;
  min-width: 100vw;
  min-height: 100vh;
  padding: min(30vh, 250px) 0;
  gap: calc(2.5rem + 0.33vh);
  background: rgba(42, 42, 42, 0.7058823529);
  backdrop-filter: blur(10px);
}
#dropdown-box.active a {
  color: #ebd693;
  font-size: 1.14rem;
  text-transform: uppercase;
  min-width: 100vw;
  margin-left: 0;
}

@media only screen and (max-width: 650px) {
  #dropdown-box.active > #home-tab > #home-heading {
    /* font-family: 'Rozha One', serif; */
    color: #ebd693;
    text-transform: uppercase;
    font-size: 1.14rem;
    text-shadow: none;
    letter-spacing: 0;
    font-weight: 400;
  }
  #right-side {
    display: none;
  }
  #dropdown-box.active a.active {
    text-decoration: underline;
    text-decoration-color: #4fa9cc;
    text-decoration-thickness: 3px;
  }
  #menu-button {
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    margin-right: 2.5rem;
    width: max-content;
    height: max-content;
    background-color: #2a2a2a;
    border: none;
    z-index: 999;
    cursor: pointer;
  }
  #menu-button .line {
    width: 3rem;
    height: 0.3rem;
    border-radius: 100px;
    background-color: #ebd693;
    transform-origin: left center;
    transition: rotate 0.5s, width 0.5s, translate 0.5s, opacity 0.5s, background-color 0.5s;
  }
  #menu-button.active #line2 {
    width: 0;
    opacity: 0;
  }
  #menu-button.active #line1, #menu-button.active #line3 {
    background-color: #4fa9cc;
  }
  #menu-button.active #line1 {
    translate: 0 -0.15rem;
    width: 2.4038rem;
    rotate: 45deg;
  }
  #menu-button.active #line3 {
    translate: 0 0.15rem;
    width: 2.4038rem;
    rotate: -45deg;
  }
  #home-tab {
    margin-left: 3rem;
  }
