#menu-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 0.9fr));
  justify-content: center;
  align-self: center;
  gap: 3rem;
  /* flex-grow: 1; */
  width: 85vw;
  min-height: 95%;
  background-color: rgba(42, 42, 42, 0.9);
  padding: 5vh 10vw;
  margin: 5vh 0;
  border: 1px solid #ebd693;
}

@media only screen and (max-width: 600px) {
  #menu-container {
    grid-template-columns: repeat(auto-fit, minmax(300px, 0.9fr));
  }
}
.menu-section {
  max-width: 80vw;
}

.section-heading {
  text-transform: uppercase;
  color: #4fa9cc;
  border-bottom: 1px solid #4fa9cc;
  font-size: clamp(1.4rem, 1.5vw, 1.8rem);
}

.section-heading, .menu-item {
  margin: 0.5rem;
}

.menu-item ~ div:not(.menu-option) {
  color: red;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-item:not(.item-option) {
  margin-top: 1rem;
  font-size: clamp(1rem, 1.8vw, 1.2rem);
}

.menu-item-name {
  color: #ebd693;
  text-transform: capitalize;
}

.item-option {
  margin-top: 0;
  margin-bottom: 0.4rem;
}

.item-option-text {
  color: white;
  font-size: clamp(0.7rem, 0.8vw, 1rem);
}

.item-option-text::before {
  content: " ";
  white-space: pre;
}

.menu-item-price {
  color: white;
  font-size: clamp(0.9rem, 1.5vw, 1.2rem);
}

.menu-item-price::before {
  content: "£";
