___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1.5;
}

#contact-container {
  flex: 1;
  align-self: center;
  padding: 0 0;
  margin-top: 2.5vh;
  width: clamp(300px, 60vw, 1000px);
  display: grid;
  grid-template-columns: auto;
  grid-auto-rows: auto;
  text-align: center;
}

.contact-section {
  display: flex;
  padding: 0.5rem;
}

.box {
  flex: 1;
  min-width: 50%;
  height: 100%;
  padding-block: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #2a2a2a;
}

#contact-info-box {
  color: white;
  background-color: #2a2a2a;
}

.contact-info {
  font-size: clamp(0.8rem, 16px, 1rem);
}

.contact-heading {
  font-size: clamp(1rem, 20px, 1.2rem);
  color: #4fa9cc;
}

.contact-heading:not(:first-child) {
  margin-top: 1rem;
}

#map-box {
  width: clamp(200px, 80%, 450px);
  min-height: 300px;
  margin: auto;
  background-color: #2a2a2a;
}

#map {
  width: 90%;
  height: 90%;
  margin: auto;
  border: 2px solid #4fa9cc;
  border-radius: 10px;
}

#message-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#message-box .msg {
  font-size: clamp(0.8rem, 16px, 1rem);
  color: white;
  margin-bottom: 1.5rem;
  width: clamp(100px, 80%, 800px);
}
#message-box .msg-bold {
  font-size: clamp(0.9rem, 19px, 1.2rem);
  font-weight: 700;
  color: #4fa9cc;
}

#social-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#social-box .social-text {
  font-size: clamp(1rem, 22px, 1.2rem);
  color: #ebd693;
}
#social-box .social-name {
  font-size: calc(clamp(1rem, 22px, 1.2rem) * 1.2);
  font-weight: 700;
}
#social-box svg {
  max-width: 50px;
  margin-block: 2rem;
}

@media only screen and (max-width: 900px) {
  #contact-container {
    min-width: 95vw;
  }
  .contact-section {
    flex-direction: column;
    min-width: 95vw;
  }
  .box {
    min-width: 100%;
    height: 50%;
  }
  .box:not(:first-child) {
    border-top: 1px solid #ebd693;
    border-top-style: hidden;
  }
